@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800&display=swap);
body,
html {
    width: 100%;
    height: auto;
    padding: 0;
    margin: 0;

    font-family: "Poppins", sans-serif;
}

*,
p {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    text-decoration: none;
    outline: none;
}

.footer {
  margin: 2rem auto;
  margin-top: 3rem;
  max-width: 60rem;
}

.footer .footertop {
  margin: 2rem auto;
  margin-top: 3rem;
  max-width: 60rem;
  display: flex;
  justify-content: space-between;
  align-items: start;
  flex-wrap: nowrap;
  flex-direction: row;
}

.footer .footertop a {
  padding-top: 0.2rem;
  text-transform: capitalize;
  font-size: 1.1rem;
  color: black;
}

.footer__contact img {
  width: 90px;
}

.footer__contact--main {
  display: flex;
  justify-content: start;
  align-items: start;
  flex-wrap: nowrap;
  flex-direction: column;
  padding-top: 2rem;
}

.footer__contact--main a {
  padding-top: 0.2rem;
  text-transform: capitalize;
  font-size: 1.1rem;
  color: black;
  text-decoration: none !important;
}

.footer__contact--gettouch {
  padding-top: 1.5rem;
}

.footer__contact--gettouch h1 {
  font-size: 1.2rem;
  text-transform: capitalize;
}

.footer__contact--gettouch-list a:not(:last-of-type) {
  padding-right: 0.5rem;
}

.footer__contact--gettouch-list a img {
  width: 20px;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.footer__contact--gettouch-list a img:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

.footer__about h1 {
  font-size: 1.3rem;
  text-transform: uppercase;
}

.footer__about h1 a {
  padding-top: 0.2rem;
  text-transform: capitalize;
  font-size: 1.1rem;
  color: black;
}

.footer__about--list a {
  display: block;
  margin-top: 0.3rem;
  text-decoration: none !important;
}

.footer__services {
  display: flex;
  justify-content: start;
  align-items: start;
  flex-wrap: nowrap;
  flex-direction: column;
}

.footer__services h1 {
  font-size: 1.3rem;
  text-transform: uppercase;
}

.footer__services h1 a {
  padding-top: 0.2rem;
  text-transform: capitalize;
  font-size: 1.1rem;
  color: black;
}

.footer__services--list a {
  display: block;
  margin-top: 0.3rem;
  text-decoration: none !important;
}

.footer__information h1 {
  font-size: 1.3rem;
  text-transform: uppercase;
}

.footer__information h1 a {
  padding-top: 0.2rem;
  text-transform: capitalize;
  font-size: 1.1rem;
  color: black;
}

.footer__information--list a {
  display: block;
  margin-top: 0.3rem;
  text-decoration: none !important;
}

.footer__copy {
  border-top: 1px solid rgba(0, 0, 0, 0.4);
  padding-top: 0.3rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  flex-wrap: nowrap;
  flex-direction: row;
}

.footer__copy h1 {
  font-size: 1rem;
  text-transform: capitalize;
  margin-top: 2px;
}

.footer__copy img {
  width: 20px;
  margin-right: 0.4rem;
}

.formlogin {
  width: 100%;
  margin: 0 !important;
}

.formlogin h1 {
  text-align: center;
}

.formlogin input {
  width: 100%;
}

.formlogin__bottom {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.formsignup {
  width: 100%;
  margin-bottom: 1rem;
  margin: 0 !important;
}

.formsignup button {
  padding: 100px;
}

.formsignup button {
  text-transform: uppercase;
  margin-top: 0.6rem;
  padding: 0.5rem 1.3rem;
  background: #ed6c63;
  color: aliceblue;
  border: none;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.formsignup button:hover {
  background: black;
}

.formsignup h1 {
  text-align: center;
}

.formsignup input {
  width: 100%;
}

.formsignup__bottom {
  margin-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.formmain {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.6);
  z-index: 1000;
  color: #fff;
}

.formmain .container {
  position: relative;
  width: 300px;
  top: 48%;
  transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -moz-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-wrap: nowrap;
  flex-direction: row;
}

.formmain .container > div {
  transition: all 0.3s ease-in-out;
  width: 100% !important;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.formmain .container > div h1 {
  text-align: center;
}

.formmain .container > div:nth-of-type(1) {
  opacity: 0;
}

.formmain .container > div:nth-of-type(2) {
  position: absolute;
  left: 100%;
  top: 0;
  opacity: 0;
  pointer-events: none;
}

.formmain button {
  text-transform: uppercase;
  margin-top: 0.6rem;
  padding: 0.5rem 1.3rem;
  background: #ed6c63;
  color: aliceblue;
  border: none;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.formmain button:hover {
  background: black;
}

.formmain__icon {
  width: 30px;
  height: 30px;
  position: absolute;
  right: 0;
  margin: 1.5rem;
}

.active {
  left: 0 !important;
  opacity: 1 !important;
  pointer-events: unset !important;
}

.activelg {
  opacity: 1 !important;
}

.header__top {
  padding: 1rem 0rem;
  border-bottom: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__top--right {
  margin-left: 4rem;
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__top--right > div {
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__top--right > div:last-of-type p {
  margin-right: 0;
}

.header__top--left {
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__top--left > * {
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__top--left-item {
  text-transform: capitalize;
}

.header__top--left-item p {
  margin: 0;
  margin-right: 2rem;
}

.header__top--left-item img {
  width: 20px;
  height: auto;
  margin-right: 0.3rem;
}

.header__center {
  padding: 1rem 2rem;
  margin: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__center--logo {
  width: 150px;
}

.header__center--right {
  text-transform: capitalize;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__center--right .help {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__center--right .help p {
  margin: 0;
}

.header__center--form {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  margin-left: 2rem;
  -webkit-border-radius: 4px;
  -moz-border-radius: 4px;
  -ms-border-radius: 4px;
  -o-border-radius: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.header__center--form button {
  height: 100%;
  padding: 0.4rem 1rem;
  border: none;
  background: #ed6c63;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  color: #fff;
  font-size: 1.2rem;
}

.header__center--form button svg {
  margin-top: -0.3rem;
}

.header__center--form input[type="text"] {
  font-size: 1.2rem;
  padding: 0.3rem 1.4rem;
  border: none;
}

.user {
  margin-right: 2rem;
}

.user > div:nth-of-type(1) {
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.user > div:nth-of-type(1) img {
  width: 20px;
}

.user > div:nth-of-type(1) p {
  margin: 0;
  margin-left: 0.4rem;
}

