.cart {
  margin-right: 2rem;
}

.cart > div:nth-of-type(1) {
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.cart > div:nth-of-type(1) img {
  width: 24px;
  cursor: pointer;
}

.cart > div:nth-of-type(1) p {
  margin: 0;
  margin-left: 0.4rem;
  cursor: pointer;
}

.cart__list {
  position: fixed;
  top: 0;
  left: 0;
  display: none;
  width: 100%;
  height: 100%;
  margin-left: auto;
  z-index: 100;
  background: rgba(0, 0, 0, 0.1);
}

.cart__list > .container {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: 0 auto;
  padding-right: 0;
  max-height: 80vh !important;
  display: flex;
  justify-content: stretch;
  align-items: stretch;
  flex-wrap: nowrap;
  flex-direction: row;
  background: #fff;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  -o-transform: translate(-50%, -50%);
}

.cart__list--content {
  width: 65%;
  overflow-y: scroll;
  margin: 2rem 0;
}

.cart__list--content::-webkit-scrollbar {
  opacity: 0;
}

.cart__list--content-top {
  padding: 1rem 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.cart__list--content-top img {
  width: 80px !important;
}

.active {
  display: unset !important;
}

.cart__list--item {
  margin-right: 2rem;
  padding: 0 1rem;
  padding-top: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: start;
  flex-wrap: nowrap;
  flex-direction: row;
}

.cart__list--item h1 {
  font-size: 1rem;
  text-align: center;
}

.cart__list--item > div {
  margin-left: 2rem;
}

.cart__list--item-infor {
  margin-right: auto;
  margin-left: 0 !important;
  display: flex;
  justify-content: stretch;
  align-items: stretch;
  flex-wrap: nowrap;
  flex-direction: row;
}

.cart__list--item-infor img {
  width: 60px !important;
}

.cart__list--item-infor-main {
  padding-left: 1rem;
}

.cart__list--item-infor-main button {
  background: #ed6c63;
  color: #fff;
}

.cart__list--item-quality-main {
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.cart__list--item-quality-main svg {
  margin: 0 0.2rem;
  cursor: pointer;
}

.cart__list--item-quality-main svg:nth-of-type(2) {
  margin-top: -6px;
}

.cart__list--pay {
  width: 100%;
  border-left: 1px solid;
  padding: 1.5rem;
}

.cart__list--pay h1 {
  font-size: 1.5rem;
  margin-top: 1rem;
}

.cart__list--pay > h1 {
  text-align: center;
  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.cart__list--pay button {
  margin-top: 2rem;
  text-transform: uppercase;
  padding: 0.5rem 1.3rem;
  background: #ed6c63;
  color: aliceblue;
  border: none;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.cart__list--pay button:hover {
  background: black;
}

.cart__list--pay-top {
  padding: 1rem 1rem;
  padding-top: 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

form {
  width: 40%;
}

.navbar {
  width: 100%;
  padding: 0;
  margin-top: 0.2rem;
  background: #e8e0e0;
}

.navbar ul {
  margin: 0 auto;
  margin-bottom: 0;
}

.navbar ul li {
  display: inline-block;
  text-align: center;
}

.navbar ul li a {
  display: inline-block;
  padding: 0 1.5rem;
  height: 50px;
  line-height: 50px;
  color: black;
  text-decoration: none;
  text-transform: uppercase;
}

.navbar ul li:hover {
  transition: all 0.3s ease-in-out;
  background: #ed6c63;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.navbar ul li:hover a {
  color: #fff;
}

.blog {
  margin: 2rem auto;
  margin-top: 3rem;
  max-width: 60rem;
}

.blog > h1 {
  font-size: 1.5rem;
  text-transform: capitalize;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-bottom: 2rem;
  position: relative;
}

.blog > h1::before {
  content: "";
  position: absolute;
  top: 110%;
  right: -50%;
  width: 150%;
  height: 3px;
  background: #ed6c63;
}

.blog__item {
  margin: 0 0.3rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.blog__item > h1 {
  text-transform: capitalize;
  font-size: 1rem;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  padding-left: 1rem;
  padding-top: 1rem;
  margin: 0;
}

.blog__item img {
  width: 100%;
}

.blog__item--infor {
  border-left: 1px solid rgba(0, 0, 0, 0.1);
  border-right: 1px solid rgba(0, 0, 0, 0.1);
  padding: 0 0.2rem;
  padding-left: 1rem;
  padding-bottom: 1rem;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: column;
  flex-direction: row !important;
}

.blog__item--infor h1 {
  font-size: 0.9rem;
  margin: 0;
  text-transform: capitalize;
}

.blog__item--infor p {
  font-size: 0.9rem;
  margin: 0;
  margin-left: 1rem;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.4);
}

.deal {
  margin: 2rem auto;
  margin-top: 3rem;
  max-width: 60rem;
}

.deal__slider {
  margin: 3rem auto;
}

.deal__right {
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
  position: relative;
}

.deal__right .prev,
.deal__right .next {
  background-color: #ed6c63 !important;
  width: 30px;
  height: 30px;
  padding: 0.2rem;
  margin-left: 0.2rem;
}

.deal__right .prev polygon,
.deal__right .next polygon {
  fill: #fff !important;
}

.deal__right .prev:hover,
.deal__right .next:hover {
  background-color: black !important;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.deal__right--btn {
  text-transform: capitalize;
  background: rgba(0, 0, 0, 0.1);
  border: none;
  padding: 0.3rem 2rem;
  margin-right: 1rem;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.deal__right--btn svg {
  width: 20px;
  height: 20px;
  margin-left: 0.1rem;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.deal__right--btn svg path {
  stroke: black !important;
}

.deal__right--btn a {
  color: black;
  text-decoration: none;
  font-weight: 500;
}

.deal__right--btn:hover {
  background: black;
}

.deal__right--btn:hover a {
  color: #fff;
}

.deal__right--btn:hover svg {
  transform: translateX(10px);
  -webkit-transform: translateX(10px);
  -moz-transform: translateX(10px);
  -ms-transform: translateX(10px);
  -o-transform: translateX(10px);
}

.deal__right--btn:hover svg path {
  stroke: #fff !important;
}

.deal__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.deal__top > h1 {
  font-size: 1.5rem;
  text-transform: capitalize;
  position: relative;
}

.deal__top > h1::before {
  content: "";
  position: absolute;
  top: 110%;
  right: -50%;
  width: 150%;
  height: 3px;
  background: #ed6c63;
}

.product__item {
  position: relative;
  margin: 1rem 0.4rem;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.product__item p {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.5rem 0.3rem;
  margin-left: 2rem;
  margin-top: 1rem;
  font-size: 0.7rem;
  background: #ed6c63;
  color: #fff;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}

.product__item--img {
  padding: 1rem 0;
  width: 100%;
  height: 60%;
  background: rgba(0, 0, 0, 0.04);
}

.product__item--img img {
  margin: 0 auto;
  width: 70%;
  height: 100%;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
}

.product__item--img img:hover {
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1);
  -o-transform: scale(1.1);
}

.product__item--infor {
  width: 80%;
  margin: 0 auto;
}

.product__item--infor h1 {
  text-transform: capitalize;
  font-size: 1rem;
  margin: 0;
  margin-top: 0.5rem;
}

.product__item--infor-bottom {
  display: flex;
  justify-content: stretch;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
}

.product__item--infor-bottom svg {
  margin-top: 0.65rem;
  padding: 0.3rem;
  box-sizing: unset;
  cursor: pointer;
}

.product__item--infor-bottom svg:hover {
  background: #ed6c63;
}

.product__item--infor-bottom svg:hover g {
  fill: #fff !important;
}

.product__item--infor-bottom h1 {
  margin-right: auto;
}

.decroration__item {
  flex-wrap: wrap !important;
  height: 38vh;
  width: 50%;
